.main-layout {
    margin: 0 80px;

    @media (max-width: 680px) {
        margin: 0 1rem;
    }
}

.main-content {
    min-height: calc(100vh - 80px);
}
.add-btn {
    @include btn;
    @include flex;
    transition: all 0.5s;
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    width: 36px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 18px;

    &::before {
        content: "+";
    }
    &::after {
        content: "";
    }

    &:hover {
        font-size: 1rem;
        width: 150px;
        &::before {
            content: "";
        }
        &::after {
            content: "Add contact";
            // opacity: 1;
            animation: content-opacity 0.5s;
        }
    }
}

@keyframes content-opacity {
    0% {
        opacity: 0;
        visibility: hidden;
    }

    100% {
        opacity: 1;
        visibility: visible;
    }
}

.large-avatar {
    height: 100px;
    width: 100px;
    background-image: url("../../assets/imgs/avatar.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    border: 2px solid $clr4;
    transform: translateY(-3px);
}