.transfer-fund {
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    @include myInput;

    input {
        height: 34px;
    }

    form {
        @include flex(row, center, space-between);
        gap: 1rem;
    }
}

.moves-list {
    height: fit-content;
    width: 100%;
    background-color: $clr2;
    color: $clr5;
    padding: 0.5rem;
    border-radius: 4px;

    .user-move {
        @include flex(column, flex-start, center);
        border-bottom: 1px solid $clr5;
        margin-bottom: .5rem;

        div {
            margin-bottom: .5rem;
        }
        span {
            font-family: domine-bold;
            color: $clr5;
        }

        .amount {
            color: $clr1;
            span {
                color: $clr5;
            }
        }
    }
}

.home-page .moves-list {
    width: 300px;
}