.app-header {
    background-color: $clr2;
    color: $clr5;
    height: rem(80px);
    @include flex(row, center, space-between);

    .logo {
        margin-left: 80px;
        font-size: rem(40px);
        font-weight: 600;
        font-family: domine-bold;
        cursor: pointer;
        span {
            color: $clr1;
        }
    }

    nav {
        margin-right: 80px;
        @include flex;
        gap: 0.5rem;

        a {
            border-bottom: 1px solid $clr2;
            transition: all 0.2s;
        }
        a:hover {
            border-bottom: 1px solid $clr1;
            color: $clr1;
        }
    }

    @media (max-width: 680px) {
        @include flex(column, center, center);

        .logo {
            margin: 0;
        }

        nav {
            margin: 0;
        }
    }
}
