@mixin flex($dir: row, $align: center, $justify: center) {
    display: flex;
    flex-direction: $dir;
    align-items: $align;
    justify-content: $justify;
}

@mixin btn($bgClr: $clr2, $clr: $clr1) {
    cursor: pointer;
    border: none;
    padding: 8px 14px;
    border-radius: 3px;
    font-size: 1rem;
    background-color: $bgClr;
    color: $clr;
    transition: all .3s;

    &:hover {
        background-color: darken($bgClr, 15%);
        color: lighten($clr, 20%);
    }
}

@mixin myInput() {
    input {
        outline: none;
        border: none;
        box-shadow: 0px 1px 9px 0px #00000040;
        height: 30px;
        width: 250px;
        padding: 10px;
        font-size: 1rem;
        background-color: $clr2;
        color: $clr5;
    }

    ::placeholder {
        color: $clr5;
        opacity: 1;
    }

    :-ms-input-placeholder {
        color: $clr5;
    }

    ::-ms-input-placeholder {
        color: $clr5;
    }
}