.contact-edit {
    padding-top: 1rem;
    form {
        width: 300px;
        @include flex(column, flex-start, flex-start);
        gap: 0.5rem;
        @include myInput;

        input {
            width: 100%;
        }

        button {
            @include btn;
            width: 50%;
            align-self: center;
        }
    }

    .goback {
        @include btn;
    }
    .delete-btn {
        @include btn($clr3, $clr1);
        &:hover {
            color: red;
        }
    }
    .btn-container {
        width: 300px;
        @include flex(row, center, space-between);
        margin-top: 0.5rem;
    }

    .large-avatar {
        margin-bottom: 0.5rem;
    }
}
