.contact-details {
    @include flex(column, flex-start);
    padding-top: 1rem;
    gap: 0.5rem;
    font-size: 1.3rem;
    color: $clr3;
    width: fit-content;

    div {
        span {
            font-family: domine-bold;
        }
    }
    button {
        @include btn;
    }

    nav {
        @include flex(row, center, space-between);
        width: 100%;
    }
    
}
